
import { computed } from 'vue'
import { useStore } from 'vuex';
import ListViewItem from './ListViewItem.vue';

export default {
  name: 'ListView',

  setup() {
    const store = useStore();
    const products = computed(() => store.state.products.items);

    return {
      ListViewItem,
      products,
      store
    }
  }
};
